import { config } from '@/utils/api.js';
export default {
	name: 'customDialog',
	props: {
		// 2 确认弹窗（领取大奖填写信息）
		customDialogType: {
			type: String,
			default: '1'
		},
		customDialogIsShow: {
			type: Boolean,
			default: false
		},
		customDialogFontTitle: {
			type: String,
			default: ''
		},
		customDialogFontCenter: {
			type: String,
			default: ''
		},
		customDialogFontP1: {
			type: String,
			default: ''
		},
		customDialogFontP2: {
			type: String,
			default: ''
		},
		customDialogFontP3: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			staticUrl: config.staticUrl,
			opacityAni: '',
			opacityAniCen: ''
		};
	},
	methods: {
		closeCustomDialog() {
			const that = this;
			that.opacityAnimation('opacityAni', 0, 500);
			that.opacityAnimation('opacityAniCen', 0, 500);
			setTimeout(() => {
				that.$emit('customDialogColse', false);
			}, 500);
		},
		// 蒙层 渐入 渐出
		opacityAnimation(name, opacityVal = 0, duration) {
			const that = this;
			// 活动规则 从下向上滑出 
			const opaAnimation = wx.createAnimation({
				duration: duration,
				timingFunction: 'ease',
				delay: 0
			});
			opaAnimation.opacity(opacityVal).step();
			that[name] = opaAnimation.export();
		},
		isStartAnimationFun(e) {
			const that = this;
			console.log(3333333);
			that.opacityAnimation('opacityAni', 0.6, 500);
			that.opacityAnimation('opacityAniCen', 1, 1000);
		}
	}
};
